<template>
    <div :config="config" class="player-collection public" :style="{width: displayWidth, 'min-height': displayHeight, zoom: displayZoom}">
        <h2 v-if="user">{{ getTrans('user_public_collection_title', {'%login%': user.login_name}) }}</h2>
        <template v-if="activities === null && achievements === null"><LoadingComp></LoadingComp></template>
        <template v-else>
            <template v-if="activities.length > 0">
                <panel v-for="activity in activities" v-if="games[activity.game_id]" :key="activity.client_id" :id="'activity-' + activity.game_id" class="game" :config="{width: '100%', height: '30px', bodyHeight: 'inherit'}">
                    <template slot="body">
                        <div :class="'row ' + (isRankable(activity.game_id) ? 'clickable' : 'unclickable')">
                            <a :href="isRankable(activity.game_id) ? urlFor('rankings-game', {'id': activity.game_id}) : ''" :class="isRankable(activity.game_id) ? 'clickable' : 'unclickable'" :target="isIn3rdParty ? '_blank' : '_self'">
                            <div class="col-md-2 col-sm-2 col-xs-3 col-content icon">
                                <img v-if="games[activity.game_id].icon" :src="games[activity.game_id].icon" class="icon game fixed" @error="gameIconError"/>
                            </div>
                            <div class="col-md-10 col-sm-10 col-xs-9 col-content info">
                                <div v-if="isRankable(activity.game_id)" class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                                <h2>{{ games[activity.game_id].name }} <small><template v-if="['xs', 'xxs'].includes($mq)"><time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('L HH:mm') }}</time></template><template v-else>{{ getTrans('last_used') }} <time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('LLLL') }}</time></template></small></h2>
                                <template v-if="Object.keys(achievements).length > 0">
                                <ul class="list-inline achievements">
                                    <li v-for="ach in achievements[activity.game_id]" v-if="achievements[activity.game_id] && hasAtLeastOneValidAchievement(achievements[activity.game_id])" :key="ach.id" v-tooltip="{html: 'achievement_tooltip_' + activity.game_id + '_' + ach.id, positioning: 'bottom'}">
                                        <img :src="ach.ribbon ? ach.ribbon : ach.picture" :class="'icon'+(ach.ribbon ? ' ribbon' :'')" />
                                        <div :id="'achievement_tooltip_' + activity.game_id + '_' + ach.id" class="tooltip-content" v-html="getAchievementDescription(ach, $moment.unix(ach.awarded_ts).format('LLLL'))"></div>
                                    </li>
                                </ul>
                                </template>
                            </div>
                            </a>
                        </div>
                    </template>
                </panel>
            </template>
            <template v-else>
                <div class="row">
                    <div class="col-md-12">
                        <p><b>{{ getTrans('noplay') }}</b></p>
                    </div>
                </div>
            </template>
        </template>
    </div>
</template>

<script>
import axios from 'axios';
import store from '@/store';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import AchievementMixin from '@mixins/achievements';

export default {
    name: 'user_public_collection',

    mixins: [GenericMixin, AchievementMixin],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '50px');
        },

        userProfileId () {
            return this.config.userProfile.id;
        },

        displayZoom () {
            return (this.config.zoom ? this.config.zoom : '1');
        },
    },

    methods: {
        getCollection () {
            const that = this;
            axios.all([
                User.activities(this.userProfileId),
                User.achievements(this.userProfileId),
                Games.all(),
                Games.rankables()
            ]).then(axios.spread(function (acts, achievements, games, rankables) {
                that.rankables = rankables.data.rankingGames;
                that.games = games.data.games;
                that.achievements = achievements.data.user.achievements;
                that.user = acts.data.user;
                if (store.getters.unloggedMode) {
                    that.activities = acts.data.activities;
                } else {
                    that.activities = acts.data.user.userapps;
                }
            }));
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getCollection);
    },

    data () {
        return {
            games: null,
            activities: null,
            achievements: null,
            rankables: null,
            user: null
        };
    }
};
</script>

<style type="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/list.scss';

.player-collection.public /deep/ {
    h2 {
        color: map-get($asnet-colors, dark-purple);
    }
    .info {
        h2 {
            color: map-get($asnet-colors, light-purple);
        }
    }
}

.panel.game /deep/ {
    .panel-body {
        height: inherit;
        padding: 0;
    }

    h2 {
        font-size: 1.5em;
        max-height: 140px;
        margin-top: 0;
    }

    img.icon {
        height: 100%;
        margin-bottom: 10px;
        margin-top: 10px;
        max-height: 100px;
        max-width: 100px;
        min-height: 40px;
        min-width: 40px;
        width: 100%;
    }

    .list-inline.achievements {
        li {
            min-height: 30px;
            min-width: 30px;
            max-height: 60px;
            max-width: 80px;

            img.icon {
                &.ribbon {
                    height: 100%;
                    min-height: 20px;
                }
            }
        }
    }
    a {
        color: #333;
        text-decoration: none;
    }

    .panel-body {
        .row {
            margin-left: 0;
            margin-right: 0;

            &.clickable:hover {
                background-color: lightgray;
                cursor: pointer;

                .fa-chevron-right {
                    color: white;
                }
            }

            .chevron-action {
                bottom: -25px;
            }

            .col-content {
                padding: 0 10px 0 0;

                &.icon {
                    text-align: center;
                    vertical-align: middle;
                }
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    .player-collection.public .panel.game .info h2 {
        font-size: 1em;
    }
}
</style>



